import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import RippleGrid from '../components/RippleGrid';
import GooeyNav from '../components/GooeyNav';
import Modal from '../components/Modal';
import ConfirmModal from '../components/ConfirmModal';
import RejectModal from '../components/RejectModal';
import UserCredentialsModal from '../components/UserCredentialsModal';
import { ReactComponent as EuroLogo } from '../assets/logos/euro-logo.svg';
import { notifyVideoApproved, notifyVideoRejected, notifyInfo } from '../utils/notificationManager';
import './AdminPage.css';

function AdminPage() {
  const navigate = useNavigate();
  const [activeTab, setActiveTab] = useState('users'); // users, queue, notifications, approved
  const [users, setUsers] = useState([]);
  const [queue, setQueue] = useState([]);
  const [approvedVideos, setApprovedVideos] = useState([]);
  const [modalState, setModalState] = useState({
    isOpen: false,
    title: '',
    message: '',
    type: 'info'
  });

  const [credentialsModal, setCredentialsModal] = useState({
    isOpen: false,
    username: '',
    password: '',
    role: ''
  });

  // –§–æ—Ä–º–∞ —Å–æ–∑–¥–∞–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  const [newUser, setNewUser] = useState({
    firstName: '',
    lastName: '',
    middleName: '',
    role: 'user'
  });

  // –§–æ—Ä–º–∞ —Ä–∞—Å—Å—ã–ª–∫–∏ —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–π
  const [notification, setNotification] = useState({
    recipient: 'all', // all –∏–ª–∏ –∫–æ–Ω–∫—Ä–µ—Ç–Ω—ã–π username
    title: '',
    message: ''
  });

  // –°–æ—Å—Ç–æ—è–Ω–∏–µ –¥–ª—è –ø–æ–∫–∞–∑–∞/—Å–∫—Ä—ã—Ç–∏—è –ø–∞—Ä–æ–ª–µ–π
  const [visiblePasswords, setVisiblePasswords] = useState({});

  // –°–æ—Å—Ç–æ—è–Ω–∏–µ –¥–ª—è –ø–æ–∏—Å–∫–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π
  const [searchQuery, setSearchQuery] = useState('');

  // –°–æ—Å—Ç–æ—è–Ω–∏–µ –¥–ª—è –ø–∞–≥–∏–Ω–∞—Ü–∏–∏
  const [currentPage, setCurrentPage] = useState(1);
  const usersPerPage = 4;

  // –°–æ—Å—Ç–æ—è–Ω–∏–µ –¥–ª—è –ø–∞–≥–∏–Ω–∞—Ü–∏–∏ –æ–¥–æ–±—Ä–µ–Ω–Ω—ã—Ö –≤–∏–¥–µ–æ
  const [approvedCurrentPage, setApprovedCurrentPage] = useState(1);
  const approvedVideosPerPage = 4;

  // –°–æ—Å—Ç–æ—è–Ω–∏–µ –¥–ª—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è —É–¥–∞–ª–µ–Ω–∏—è
  const [deleteConfirm, setDeleteConfirm] = useState({
    isOpen: false,
    username: '',
    userFullName: ''
  });

  // –°–æ—Å—Ç–æ—è–Ω–∏–µ –¥–ª—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –æ—Ç–∫–ª–æ–Ω–µ–Ω–∏—è –≤–∏–¥–µ–æ
  const [rejectModal, setRejectModal] = useState({
    isOpen: false,
    video: null,
    index: -1
  });

  useEffect(() => {
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—é –∏ –ø—Ä–∞–≤–∞ –∞–¥–º–∏–Ω–∞
    const user = localStorage.getItem('user');
    if (!user) {
      navigate('/');
      return;
    }

    const userData = JSON.parse(user);
    if (userData.role !== 'admin') {
      navigate('/home');
      return;
    }

    loadUsers();
    loadQueue();
    loadApprovedVideos();
  }, [navigate]);

  const loadUsers = () => {
    const savedUsers = localStorage.getItem('users');
    if (savedUsers) {
      const usersData = JSON.parse(savedUsers);
      setUsers(usersData.users || []);
    }
  };

  const loadQueue = () => {
    const savedQueue = localStorage.getItem('adminQueue');
    const queueData = savedQueue ? JSON.parse(savedQueue) : [];
    setQueue(queueData);
  };

  const loadApprovedVideos = () => {
    const savedApproved = localStorage.getItem('approvedVideos');
    const approvedData = savedApproved ? JSON.parse(savedApproved) : [];
    setApprovedVideos(approvedData);
  };

  const generateRandomString = (length) => {
    const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    let result = '';
    for (let i = 0; i < length; i++) {
      result += chars.charAt(Math.floor(Math.random() * chars.length));
    }
    return result;
  };

  const handleCreateUser = () => {
    if (!newUser.firstName || !newUser.lastName) {
      setModalState({
        isOpen: true,
        title: '–û—à–∏–±–∫–∞',
        message: '–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –∏–º—è –∏ —Ñ–∞–º–∏–ª–∏—é',
        type: 'error'
      });
      return;
    }

    const username = generateRandomString(8);
    const password = generateRandomString(8);

    const userToAdd = {
      username,
      password,
      role: newUser.role,
      firstName: newUser.firstName,
      lastName: newUser.lastName,
      middleName: newUser.middleName,
      createdAt: Date.now()
    };

    const updatedUsers = [...users, userToAdd];
    setUsers(updatedUsers);

    // –°–æ—Ö—Ä–∞–Ω—è–µ–º –≤ localStorage
    localStorage.setItem('users', JSON.stringify({ users: updatedUsers }));

    // –ü–æ–∫–∞–∑—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ —Å —É—á—ë—Ç–Ω—ã–º–∏ –¥–∞–Ω–Ω—ã–º–∏
    setCredentialsModal({
      isOpen: true,
      username: username,
      password: password,
      role: newUser.role
    });

    // –û—á–∏—â–∞–µ–º —Ñ–æ—Ä–º—É
    setNewUser({
      firstName: '',
      lastName: '',
      middleName: '',
      role: 'user'
    });
  };

  const handleDeleteUserClick = (user) => {
    if (user.username === 'Admin') {
      setModalState({
        isOpen: true,
        title: '–û—à–∏–±–∫–∞',
        message: '–ù–µ–ª—å–∑—è —É–¥–∞–ª–∏—Ç—å –≥–ª–∞–≤–Ω–æ–≥–æ –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞',
        type: 'error'
      });
      return;
    }

    // –û—Ç–∫—Ä—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è
    setDeleteConfirm({
      isOpen: true,
      username: user.username,
      userFullName: `${user.lastName} ${user.firstName} ${user.middleName || ''}`.trim()
    });
  };

  const handleConfirmDelete = () => {
    const username = deleteConfirm.username;
    const updatedUsers = users.filter(u => u.username !== username);
    setUsers(updatedUsers);
    localStorage.setItem('users', JSON.stringify({ users: updatedUsers }));

    setModalState({
      isOpen: true,
      title: '–£—Å–ø–µ—à–Ω–æ',
      message: '–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —É–¥–∞–ª—ë–Ω',
      type: 'success'
    });
  };

  // –§–∏–ª—å—Ç—Ä–∞—Ü–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π –ø–æ –ø–æ–∏—Å–∫–æ–≤–æ–º—É –∑–∞–ø—Ä–æ—Å—É
  const filteredUsers = users.filter(user => {
    if (!searchQuery.trim()) return true;
    
    const query = searchQuery.toLowerCase();
    return (
      user.username.toLowerCase().includes(query) ||
      user.firstName.toLowerCase().includes(query) ||
      user.lastName.toLowerCase().includes(query) ||
      (user.middleName && user.middleName.toLowerCase().includes(query))
    );
  });

  // –ü–æ–¥—Å—á—ë—Ç –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π –∏ –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–æ–≤
  const regularUsersCount = filteredUsers.filter(user => user.role === 'user').length;
  const adminsCount = filteredUsers.filter(user => user.role === 'admin').length;

  // –ü–∞–≥–∏–Ω–∞—Ü–∏—è
  const totalPages = Math.ceil(filteredUsers.length / usersPerPage);
  const startIndex = (currentPage - 1) * usersPerPage;
  const endIndex = startIndex + usersPerPage;
  const currentUsers = filteredUsers.slice(startIndex, endIndex);

  // –°–±—Ä–æ—Å –Ω–∞ –ø–µ—Ä–≤—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –ø–æ–∏—Å–∫–∞
  const handleSearchChange = (value) => {
    setSearchQuery(value);
    setCurrentPage(1);
  };

  // –§—É–Ω–∫—Ü–∏–∏ –¥–ª—è –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ –ø–æ —Å—Ç—Ä–∞–Ω–∏—Ü–∞–º
  const goToPage = (page) => {
    if (page >= 1 && page <= totalPages) {
      setCurrentPage(page);
    }
  };

  const goToNextPage = () => {
    if (currentPage < totalPages) {
      setCurrentPage(currentPage + 1);
    }
  };

  const goToPrevPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  const handleApproveVideo = (video, index) => {
    // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é
    notifyVideoApproved(video.username, video.fileName);

    // –î–æ–±–∞–≤–ª—è–µ–º –≤ —Å–ø–∏—Å–æ–∫ –æ–¥–æ–±—Ä–µ–Ω–Ω—ã—Ö –≤–∏–¥–µ–æ
    const approvedVideo = {
      ...video,
      approvedAt: Date.now()
    };
    const updatedApproved = [approvedVideo, ...approvedVideos];
    setApprovedVideos(updatedApproved);
    localStorage.setItem('approvedVideos', JSON.stringify(updatedApproved));

    // –£–¥–∞–ª—è–µ–º –∏–∑ –æ—á–µ—Ä–µ–¥–∏
    const updatedQueue = queue.filter((_, i) => i !== index);
    setQueue(updatedQueue);
    localStorage.setItem('adminQueue', JSON.stringify(updatedQueue));

    setModalState({
      isOpen: true,
      title: '–û–¥–æ–±—Ä–µ–Ω–æ',
      message: `–í–∏–¥–µ–æ "${video.fileName}" –æ–¥–æ–±—Ä–µ–Ω–æ. –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –ø–æ–ª—É—á–∏—Ç —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ.`,
      type: 'success'
    });
  };

  const handleRejectVideoClick = (video, index) => {
    setRejectModal({
      isOpen: true,
      video: video,
      index: index
    });
  };

  const handleConfirmReject = (reason) => {
    const { video, index } = rejectModal;
    
    // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é
    notifyVideoRejected(video.username, video.fileName, reason || '');

    // –£–¥–∞–ª—è–µ–º –∏–∑ –æ—á–µ—Ä–µ–¥–∏
    const updatedQueue = queue.filter((_, i) => i !== index);
    setQueue(updatedQueue);
    localStorage.setItem('adminQueue', JSON.stringify(updatedQueue));

    // –ó–∞–∫—Ä—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –æ—Ç–∫–ª–æ–Ω–µ–Ω–∏—è
    setRejectModal({
      isOpen: false,
      video: null,
      index: -1
    });

    // –ü–æ–∫–∞–∑—ã–≤–∞–µ–º —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –æ–± —É—Å–ø–µ—Ö–µ
    setModalState({
      isOpen: true,
      title: '–û—Ç–∫–ª–æ–Ω–µ–Ω–æ',
      message: `–í–∏–¥–µ–æ "${video.fileName}" –æ—Ç–∫–ª–æ–Ω–µ–Ω–æ. –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –ø–æ–ª—É—á–∏—Ç —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ.`,
      type: 'info'
    });
  };

  const handleClearAllApproved = () => {
    if (approvedVideos.length === 0) return;

    // –û—á–∏—â–∞–µ–º —Å–ø–∏—Å–æ–∫ –æ–¥–æ–±—Ä–µ–Ω–Ω—ã—Ö –≤–∏–¥–µ–æ
    setApprovedVideos([]);
    localStorage.setItem('approvedVideos', JSON.stringify([]));
    
    // –°–±—Ä–∞—Å—ã–≤–∞–µ–º –Ω–∞ –ø–µ—Ä–≤—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É
    setApprovedCurrentPage(1);

    setModalState({
      isOpen: true,
      title: '–û—á–∏—â–µ–Ω–æ',
      message: '–í—Å–µ –æ–¥–æ–±—Ä–µ–Ω–Ω—ã–µ –≤–∏–¥–µ–æ —É–¥–∞–ª–µ–Ω—ã –∏–∑ —Å–ø–∏—Å–∫–∞.',
      type: 'success'
    });
  };

  const handleSendNotification = () => {
    if (!notification.title || !notification.message) {
      setModalState({
        isOpen: true,
        title: '–û—à–∏–±–∫–∞',
        message: '–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –∑–∞–≥–æ–ª–æ–≤–æ–∫ –∏ —Å–æ–æ–±—â–µ–Ω–∏–µ',
        type: 'error'
      });
      return;
    }

    if (notification.recipient === 'all') {
      // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –≤—Å–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è–º
      users.forEach(user => {
        notifyInfo(user.username, notification.title, notification.message);
      });

      setModalState({
        isOpen: true,
        title: '–û—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ',
        message: `–£–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ –≤—Å–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è–º (${users.length})`,
        type: 'success'
      });
    } else {
      // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –∫–æ–Ω–∫—Ä–µ—Ç–Ω–æ–º—É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é
      notifyInfo(notification.recipient, notification.title, notification.message);

      setModalState({
        isOpen: true,
        title: '–û—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ',
        message: `–£–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é ${notification.recipient}`,
        type: 'success'
      });
    }

    // –û—á–∏—â–∞–µ–º —Ñ–æ—Ä–º—É
    setNotification({
      recipient: 'all',
      title: '',
      message: ''
    });
  };

  const handleNavClick = (item) => {
    if (item.action === 'home') {
      navigate('/home');
    } else if (item.action === 'history') {
      navigate('/history');
    } else if (item.action === 'notifications') {
      navigate('/notifications');
    } else if (item.action === 'admin') {
      // –£–∂–µ –Ω–∞ –∞–¥–º–∏–Ω-–ø–∞–Ω–µ–ª–∏
      return;
    } else if (item.action === 'logout') {
      localStorage.removeItem('user');
      navigate('/');
    }
  };

  const navItems = [
    { label: '–ì–ª–∞–≤–Ω–∞—è', href: '#', action: 'home' },
    { label: '–ò—Å—Ç–æ—Ä–∏—è', href: '#', action: 'history' },
    { label: '–£–≤–µ–¥–æ–º–ª–µ–Ω–∏—è', href: '#', action: 'notifications' },
    { label: '–ê–¥–º–∏–Ω', href: '#', action: 'admin' },
    { label: '–í—ã—Ö–æ–¥', href: '#', action: 'logout' }
  ];

  const closeModal = () => {
    setModalState({
      isOpen: false,
      title: '',
      message: '',
      type: 'info'
    });
  };

  return (
    <div className="admin-page">
      {/* Ripple Grid —Ñ–æ–Ω */}
      <div className="admin-background">
        <RippleGrid
          enableRainbow={false}
          gridColor="#8a5cff"
          rippleIntensity={0.01}
          gridSize={20}
          gridThickness={20}
          fadeDistance={2.5}
          vignetteStrength={1.5}
          glowIntensity={0.2}
          mouseInteraction={true}
          mouseInteractionRadius={1.2}
          opacity={0.9}
        />
      </div>

      {/* –õ–æ–≥–æ—Ç–∏–ø –∏ –Ω–∞–≤–∏–≥–∞—Ü–∏—è */}
      <div className="page-header">
        <div className="page-logo">
          <EuroLogo />
        </div>
        <GooeyNav
          items={navItems}
          particleCount={15}
          particleDistances={[90, 10]}
          particleR={100}
          initialActiveIndex={3}
          animationTime={600}
          timeVariance={300}
          colors={[1, 2, 3, 1, 2, 3, 1, 4]}
          onItemClick={handleNavClick}
        />
      </div>

      {/* –°–æ–¥–µ—Ä–∂–∏–º–æ–µ –∞–¥–º–∏–Ω-–ø–∞–Ω–µ–ª–∏ */}
      <div className="admin-content">
        <h1 className="admin-title">–ü–∞–Ω–µ–ª—å –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞</h1>

        {/* –í–∫–ª–∞–¥–∫–∏ */}
        <div className="admin-tabs">
          <button
            className={`tab-btn ${activeTab === 'users' ? 'active' : ''}`}
            onClick={() => setActiveTab('users')}
          >
            –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏
          </button>
          <button
            className={`tab-btn ${activeTab === 'queue' ? 'active' : ''}`}
            onClick={() => setActiveTab('queue')}
          >
            –û—á–µ—Ä–µ–¥—å ({queue.length})
          </button>
          <button
            className={`tab-btn ${activeTab === 'approved' ? 'active' : ''}`}
            onClick={() => setActiveTab('approved')}
          >
            –û–¥–æ–±—Ä–µ–Ω–Ω—ã–µ ({approvedVideos.length})
          </button>
          <button
            className={`tab-btn ${activeTab === 'notifications' ? 'active' : ''}`}
            onClick={() => setActiveTab('notifications')}
          >
            –†–∞—Å—Å—ã–ª–∫–∞
          </button>
        </div>

        {/* –ö–æ–Ω—Ç–µ–Ω—Ç –≤–∫–ª–∞–¥–æ–∫ */}
        <div className="tab-content">
          {/* –í–∫–ª–∞–¥–∫–∞: –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏ */}
          {activeTab === 'users' && (
            <div className="users-tab">
              <div className="create-user-form">
                <h2>–°–æ–∑–¥–∞—Ç—å –Ω–æ–≤–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è</h2>
                <div className="form-grid">
                  <input
                    type="text"
                    placeholder="–§–∞–º–∏–ª–∏—è"
                    value={newUser.lastName}
                    onChange={(e) => setNewUser({ ...newUser, lastName: e.target.value })}
                  />
                  <input
                    type="text"
                    placeholder="–ò–º—è"
                    value={newUser.firstName}
                    onChange={(e) => setNewUser({ ...newUser, firstName: e.target.value })}
                  />
                  <input
                    type="text"
                    placeholder="–û—Ç—á–µ—Å—Ç–≤–æ (–Ω–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ)"
                    value={newUser.middleName}
                    onChange={(e) => setNewUser({ ...newUser, middleName: e.target.value })}
                  />
                  <select
                    value={newUser.role}
                    onChange={(e) => setNewUser({ ...newUser, role: e.target.value })}
                  >
                    <option value="user">–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å</option>
                    <option value="admin">–ê–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä</option>
                  </select>
                </div>
                <button className="btn-create-user" onClick={handleCreateUser}>
                  –°–æ–∑–¥–∞—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
                </button>
              </div>

              <div className="users-list">
                <div className="users-list-header">
                  <h2>–°–ø–∏—Å–æ–∫ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π ({regularUsersCount} / {adminsCount})</h2>
                  <div className="search-box">
                    <input
                      type="text"
                      placeholder="–ü–æ–∏—Å–∫ –ø–æ –ª–æ–≥–∏–Ω—É, –∏–º–µ–Ω–∏ –∏–ª–∏ —Ñ–∞–º–∏–ª–∏–∏..."
                      value={searchQuery}
                      onChange={(e) => handleSearchChange(e.target.value)}
                      className="search-input"
                    />
                    {searchQuery && (
                      <button
                        className="search-clear"
                        onClick={() => handleSearchChange('')}
                        title="–û—á–∏—Å—Ç–∏—Ç—å –ø–æ–∏—Å–∫"
                      >
                        √ó
                      </button>
                    )}
                  </div>
                </div>
                {filteredUsers.length === 0 ? (
                  <div className="no-users-found">
                    <p>–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏ –Ω–µ –Ω–∞–π–¥–µ–Ω—ã</p>
                  </div>
                ) : (
                  <>
                    {currentUsers.map((user, index) => (
                      <div key={index} className="user-item">
                        <div className="user-info">
                          <h3>{user.lastName} {user.firstName} {user.middleName}</h3>
                          <p>–õ–æ–≥–∏–Ω: <strong>{user.username}</strong></p>
                          <p className="password-field">
                            –ü–∞—Ä–æ–ª—å: 
                            <strong className="password-value">
                              {visiblePasswords[user.username] ? user.password : '‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢'}
                            </strong>
                            <button
                              className="btn-toggle-password-inline"
                              onClick={() => setVisiblePasswords({
                                ...visiblePasswords,
                                [user.username]: !visiblePasswords[user.username]
                              })}
                              title={visiblePasswords[user.username] ? '–°–∫—Ä—ã—Ç—å –ø–∞—Ä–æ–ª—å' : '–ü–æ–∫–∞–∑–∞—Ç—å –ø–∞—Ä–æ–ª—å'}
                            >
                              {visiblePasswords[user.username] ? 'üëÅÔ∏è' : 'üëÅÔ∏è‚Äçüó®Ô∏è'}
                            </button>
                          </p>
                          <p>–†–æ–ª—å: <span className={`role-badge ${user.role}`}>{user.role === 'admin' ? '–ê–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä' : '–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å'}</span></p>
                        </div>
                        <button
                          className="btn-delete-user"
                          onClick={() => handleDeleteUserClick(user)}
                          disabled={user.username === 'Admin'}
                        >
                          –£–¥–∞–ª–∏—Ç—å
                        </button>
                      </div>
                    ))}

                    {/* –ü–∞–≥–∏–Ω–∞—Ü–∏—è */}
                    {totalPages > 1 && (
                      <div className="pagination">
                        <button
                          className="pagination-btn"
                          onClick={goToPrevPage}
                          disabled={currentPage === 1}
                        >
                          ‚Üê –ù–∞–∑–∞–¥
                        </button>
                        
                        <div className="pagination-pages">
                          {Array.from({ length: totalPages }, (_, i) => i + 1).map(page => (
                            <button
                              key={page}
                              className={`pagination-page ${page === currentPage ? 'active' : ''}`}
                              onClick={() => goToPage(page)}
                            >
                              {page}
                            </button>
                          ))}
                        </div>

                        <button
                          className="pagination-btn"
                          onClick={goToNextPage}
                          disabled={currentPage === totalPages}
                        >
                          –í–ø–µ—Ä—ë–¥ ‚Üí
                        </button>
                      </div>
                    )}
                  </>
                )}
              </div>
            </div>
          )}

          {/* –í–∫–ª–∞–¥–∫–∞: –û—á–µ—Ä–µ–¥—å */}
          {activeTab === 'queue' && (
            <div className="queue-tab">
              <h2>–û—á–µ—Ä–µ–¥—å –Ω–∞ –æ–¥–æ–±—Ä–µ–Ω–∏–µ ({queue.length})</h2>
              {queue.length === 0 ? (
                <div className="empty-queue">
                  <p>–û—á–µ—Ä–µ–¥—å –ø—É—Å—Ç–∞</p>
                </div>
              ) : (
                queue.map((video, index) => (
                  <div key={index} className="queue-item">
                    {video.thumbnail && (
                      <div className="queue-thumbnail">
                        <img src={video.thumbnail} alt={video.fileName} />
                      </div>
                    )}
                    <div className="queue-info">
                      <h3>{video.fileName}</h3>
                      <p>–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å: <strong>{video.username}</strong></p>
                      <p>–î–∞—Ç–∞: {new Date(video.timestamp).toLocaleString('ru-RU')}</p>
                      <p>–ü–∞—Ä–∞–º–µ—Ç—Ä—ã: {video.resolution} ‚Ä¢ {video.duration} ‚Ä¢ {video.fileSize}</p>
                    </div>
                    <div className="queue-actions">
                      <button
                        className="btn-approve"
                        onClick={() => handleApproveVideo(video, index)}
                      >
                        ‚úì –û–¥–æ–±—Ä–∏—Ç—å
                      </button>
                      <button
                        className="btn-reject"
                        onClick={() => handleRejectVideoClick(video, index)}
                      >
                        ‚úó –û—Ç–∫–ª–æ–Ω–∏—Ç—å
                      </button>
                    </div>
                  </div>
                ))
              )}
            </div>
          )}

          {/* –í–∫–ª–∞–¥–∫–∞: –û–¥–æ–±—Ä–µ–Ω–Ω—ã–µ –≤–∏–¥–µ–æ */}
          {activeTab === 'approved' && (
            <div className="approved-tab">
              <h2>–û–¥–æ–±—Ä–µ–Ω–Ω—ã–µ –≤–∏–¥–µ–æ ({approvedVideos.length})</h2>
              {approvedVideos.length === 0 ? (
                <div className="empty-approved">
                  <p>–ù–µ—Ç –æ–¥–æ–±—Ä–µ–Ω–Ω—ã—Ö –≤–∏–¥–µ–æ</p>
                </div>
              ) : (
                <>
                  {(() => {
                    // –ü–∞–≥–∏–Ω–∞—Ü–∏—è –¥–ª—è –æ–¥–æ–±—Ä–µ–Ω–Ω—ã—Ö –≤–∏–¥–µ–æ
                    const totalPages = Math.ceil(approvedVideos.length / approvedVideosPerPage);
                    const startIndex = (approvedCurrentPage - 1) * approvedVideosPerPage;
                    const endIndex = startIndex + approvedVideosPerPage;
                    const currentApprovedVideos = approvedVideos.slice(startIndex, endIndex);

                    return (
                      <>
                        {currentApprovedVideos.map((video, index) => (
                          <div key={startIndex + index} className="approved-item">
                            {video.thumbnail && (
                              <div className="approved-thumbnail">
                                <img src={video.thumbnail} alt={video.fileName} />
                              </div>
                            )}
                            <div className="approved-info">
                              <h3>{video.fileName}</h3>
                              <p>–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å: <strong>{video.username}</strong></p>
                              <p>–ó–∞–≥—Ä—É–∂–µ–Ω–æ: {new Date(video.timestamp).toLocaleString('ru-RU')}</p>
                              <p>–û–¥–æ–±—Ä–µ–Ω–æ: {new Date(video.approvedAt).toLocaleString('ru-RU')}</p>
                              <p>–ü–∞—Ä–∞–º–µ—Ç—Ä—ã: {video.resolution} ‚Ä¢ {video.duration} ‚Ä¢ {video.fileSize}</p>
                            </div>
                          </div>
                        ))}

                        {/* –ü–∞–≥–∏–Ω–∞—Ü–∏—è –¥–ª—è –æ–¥–æ–±—Ä–µ–Ω–Ω—ã—Ö –≤–∏–¥–µ–æ */}
                        <div className="pagination">
                          {totalPages > 1 && (
                            <>
                              <button
                                className="pagination-btn"
                                onClick={() => {
                                  if (approvedCurrentPage > 1) {
                                    setApprovedCurrentPage(approvedCurrentPage - 1);
                                  }
                                }}
                                disabled={approvedCurrentPage === 1}
                              >
                                ‚Üê –ù–∞–∑–∞–¥
                              </button>
                              
                              <div className="pagination-pages">
                                {Array.from({ length: totalPages }, (_, i) => i + 1).map(page => (
                                  <button
                                    key={page}
                                    className={`pagination-page ${page === approvedCurrentPage ? 'active' : ''}`}
                                    onClick={() => setApprovedCurrentPage(page)}
                                  >
                                    {page}
                                  </button>
                                ))}
                              </div>

                              <button
                                className="pagination-btn"
                                onClick={() => {
                                  if (approvedCurrentPage < totalPages) {
                                    setApprovedCurrentPage(approvedCurrentPage + 1);
                                  }
                                }}
                                disabled={approvedCurrentPage === totalPages}
                              >
                                –í–ø–µ—Ä—ë–¥ ‚Üí
                              </button>
                            </>
                          )}

                          <button
                            className="btn-clear-approved"
                            onClick={handleClearAllApproved}
                          >
                            –û—á–∏—Å—Ç–∏—Ç—å –≤—Å—ë
                          </button>
                        </div>
                      </>
                    );
                  })()}
                </>
              )}
            </div>
          )}

          {/* –í–∫–ª–∞–¥–∫–∞: –†–∞—Å—Å—ã–ª–∫–∞ */}
          {activeTab === 'notifications' && (
            <div className="notifications-tab">
              <h2>–†–∞—Å—Å—ã–ª–∫–∞ —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–π</h2>
              <div className="notification-form">
                <select
                  value={notification.recipient}
                  onChange={(e) => setNotification({ ...notification, recipient: e.target.value })}
                >
                  <option value="all">–í—Å–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è–º</option>
                  {users.map((user, index) => (
                    <option key={index} value={user.username}>
                      {user.firstName} {user.lastName} ({user.username})
                    </option>
                  ))}
                </select>
                <input
                  type="text"
                  placeholder="–ó–∞–≥–æ–ª–æ–≤–æ–∫ —É–≤–µ–¥–æ–º–ª–µ–Ω–∏—è"
                  value={notification.title}
                  onChange={(e) => setNotification({ ...notification, title: e.target.value })}
                />
                <textarea
                  placeholder="–¢–µ–∫—Å—Ç —É–≤–µ–¥–æ–º–ª–µ–Ω–∏—è"
                  value={notification.message}
                  onChange={(e) => setNotification({ ...notification, message: e.target.value })}
                  rows={5}
                />
                <button className="btn-send-notification" onClick={handleSendNotification}>
                  –û—Ç–ø—Ä–∞–≤–∏—Ç—å —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ
                </button>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ */}
      <Modal
        isOpen={modalState.isOpen}
        onClose={closeModal}
        title={modalState.title}
        message={modalState.message}
        type={modalState.type}
      />

      {/* –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ —Å —É—á—ë—Ç–Ω—ã–º–∏ –¥–∞–Ω–Ω—ã–º–∏ */}
      <UserCredentialsModal
        isOpen={credentialsModal.isOpen}
        onClose={() => setCredentialsModal({ ...credentialsModal, isOpen: false })}
        username={credentialsModal.username}
        password={credentialsModal.password}
        role={credentialsModal.role}
      />

      {/* –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è —É–¥–∞–ª–µ–Ω–∏—è */}
      <ConfirmModal
        isOpen={deleteConfirm.isOpen}
        onClose={() => setDeleteConfirm({ ...deleteConfirm, isOpen: false })}
        onConfirm={handleConfirmDelete}
        title="–ü–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏–µ —É–¥–∞–ª–µ–Ω–∏—è"
        message={`–í—ã —É–≤–µ—Ä–µ–Ω—ã, —á—Ç–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è "${deleteConfirm.userFullName}" (${deleteConfirm.username})? –≠—Ç–æ –¥–µ–π—Å—Ç–≤–∏–µ –Ω–µ–ª—å–∑—è –æ—Ç–º–µ–Ω–∏—Ç—å.`}
        confirmText="–£–¥–∞–ª–∏—Ç—å"
        cancelText="–û—Ç–º–µ–Ω–∞"
      />

      {/* –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –æ—Ç–∫–ª–æ–Ω–µ–Ω–∏—è –≤–∏–¥–µ–æ */}
      <RejectModal
        isOpen={rejectModal.isOpen}
        onClose={() => setRejectModal({ isOpen: false, video: null, index: -1 })}
        onConfirm={handleConfirmReject}
        videoName={rejectModal.video?.fileName || ''}
      />
    </div>
  );
}

export default AdminPage;

