import React, { useState } from 'react';
import './RejectModal.css';

function RejectModal({ isOpen, onClose, onConfirm, videoName }) {
  const [reason, setReason] = useState('');

  if (!isOpen) return null;

  const handleConfirm = () => {
    onConfirm(reason);
    setReason('');
  };

  const handleClose = () => {
    setReason('');
    onClose();
  };

  return (
    <div className="reject-modal-overlay" onClick={handleClose}>
      <div className="reject-modal-content" onClick={(e) => e.stopPropagation()}>
        <div className="reject-modal-header">
          <h2>–û—Ç–∫–ª–æ–Ω–∏—Ç—å –≤–∏–¥–µ–æ</h2>
        </div>
        <div className="reject-modal-body">
          <p className="reject-modal-video-name">
            –í—ã —É–≤–µ—Ä–µ–Ω—ã, —á—Ç–æ —Ö–æ—Ç–∏—Ç–µ –æ—Ç–∫–ª–æ–Ω–∏—Ç—å –≤–∏–¥–µ–æ <strong>"{videoName}"</strong>?
          </p>
          <div className="reject-modal-input-group">
            <label htmlFor="reject-reason">–ü—Ä–∏—á–∏–Ω–∞ –æ—Ç–∫–ª–æ–Ω–µ–Ω–∏—è (–Ω–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ)</label>
            <textarea
              id="reject-reason"
              placeholder="–£–∫–∞–∂–∏—Ç–µ –ø—Ä–∏—á–∏–Ω—É –æ—Ç–∫–ª–æ–Ω–µ–Ω–∏—è..."
              value={reason}
              onChange={(e) => setReason(e.target.value)}
              rows={4}
            />
          </div>
        </div>
        <div className="reject-modal-footer">
          <button className="reject-btn-cancel" onClick={handleClose}>
            –û—Ç–º–µ–Ω–∞
          </button>
          <button className="reject-btn-confirm" onClick={handleConfirm}>
            –û—Ç–∫–ª–æ–Ω–∏—Ç—å
          </button>
        </div>
      </div>
    </div>
  );
}

export default RejectModal;

